import React, {memo, useState} from 'react';
import {View, Text, Pressable, Platform, StyleSheet, FlatList, Image} from 'react-native';
import colors from "../styles/colors";
import ContentBody from './content-body.native';

{/** Sticking with a solid theme set here, so I'll use FA! */}
import { MaterialCommunityIcons } from 'react-native-vector-icons';

function Section({title, content, isVisible, hasImage, image, imageHeader}){

    const [state, setState] = useState({
        isVisible:isVisible,
    });

    return (
        <View style={styles.itemWrapper}>
            {/** Introduction Landing Page content ...I'm awk af so here's my weirdness GGnoRE */}
            <View style={styles.itemOutterContainer}>
                <View style={styles.itemInnerContainer}>
                    <Pressable onPress={()=> setState({isVisible:!state.isVisible})}>
                        <View style={{
                            width:'100%',
                            margin:'auto',
                            backgroundColor:colors.darkBlue,
                            borderRadius:(state.isVisible) ? 0 : 20,
                            flexDirection:'row',
                            textAlign:'center',
                            justifyContent:'center',
                            minHeight:50,
                            minWidth:330,
                        }}> 
                            <View>
                                <MaterialCommunityIcons
                                    name={(state.isVisible) ? "minus-thick" : "plus-thick"}
                                    style={{
                                        color:colors.offwhite,
                                        margin:'auto',
                                    }}
                                    size={25}
                                />
                            </View>
                            <View>
                                <Text style={{
                                    fontSize: (state.isVisible) ? 24 : 22,
                                    fontFamily:'Roboto',
                                    fontWeight: '500',
                                    paddingVertical: 5,
                                    paddingHorizontal: 15,
                                    color:colors.offwhite,
                                    margin:'auto',
                                }}>
                                    <u>{title}</u>
                                </Text>
                            </View>
                            
                        </View>
                    </Pressable>
                    {   state.isVisible &&
                        <View style={styles.content}>
                            {   hasImage &&
                                    <View>
                                        <View style={styles.image}>
                                                <View>
                                                    <Image
                                                        source={image}
                                                        style={{
                                                            width:140,
                                                            height:140,
                                                            borderRadius:70,
                                                        }}
                                                    />
                                                </View>
                                        </View>
                                        <View style={styles.imageHeaderWrapper}>
                                            <Text style={styles.imageHeaderTop}>
                                                {imageHeader[0]}
                                            </Text>
                                            <Text style={styles.imageHeaderBottom}>
                                                {imageHeader[1]}
                                            </Text>
                                        </View>
                                    </View>
                            }
                            <FlatList
                                data={content}
                                keyExtractor={(item) => item.id.toString()}
                                renderItem={({item}) => (
                                    <ContentBody
                                    content={item.text}
                                    />
                                )}
                            />
                        </View>
                    }
                </View>  
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    itemOutterContainer: {
        justifyContent:'space-around',
        margin: 'auto',
        backgroundColor:colors.lightblue,
        borderRadius:30,
        minHeight:50,
        width:'90%',
        maxWidth:'90%',
    },
    itemInnerContainer:{
        backgroundColor: 'transparent',
        borderRadius:30,
        justifyContent: 'center',
    },
    itemWrapper:{
        paddingHorizontal: 20,
        paddingVertical: 10,
        justifyContent: 'center',
        width:'100%',
        
    },
    textTitle:{
        fontSize: 22,
        fontFamily:'Roboto',
        fontWeight:'500',
        paddingVertical: 5,
        color:colors.offwhite,
        textAlign:'center',
    },
    sectionTitle:{
        width:'100%',
        margin:'auto',
        justifyContent:'center',
        flexDirection:'row',
        backgroundColor:colors.darkBlue,
        borderRadius:20,
        paddingHorizontal:25,
    },
    text:{
        color: colors.darkBlue,
        paddingVertical: 15,
        fontSize:15,
        fontFamily: 'Roboto',
        paddingHorizontal:15,
    },
    content:{

    },
    image:{
        flexDirection:'row',
        justifyContent:'center',
        paddingVertical:15,
    },
    imageHeaderWrapper:{
        textAlign:'center',
        paddingVertical:15,
    },
    imageHeaderTop:{
        color:colors.darkBlue,
        fontSize:24,
        fontWeight:'500',
    },
    imageHeaderBottom:{
        color:colors.darkBlue,
        fontSize:20,
        fontWeight:'500'
    },
});

export default Section;